<template>
  <v-row justify="center" v-if="dialog">
    <v-dialog v-model="dialog" persistent max-width="400">
      <v-card 
        class="confirm-dialog py-2" 
        light
      >
        <v-card-title class="headline">
          {{ title }}
        </v-card-title>
        <v-card-text v-if="description">
          {{ description }}
        </v-card-text>
        <v-card-text>
          <slot></slot>
        </v-card-text>
        <v-card-actions class="px-4 mt-4">
          <v-spacer></v-spacer>
          <v-btn
            class="primary"
            text
            dark
            :disabled="btnDisabled"
            @click="send('confirm')"
          >
            Confirmar
          </v-btn>
          <v-btn 
            text
            @click="send('cancel')"
          >
            Cancelar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  name: 'Default',
  props: {
    btnDisabled: {
      default: false
    },
    title: {
      required: true
    },
    description: {
      required: false
    },
    danger: {
      default: false
    }
  },
  data: () => ({
    dialog: false
  }),
  methods: {
    send (emit) {
      this.dialog = false
      this.$emit(emit)
    },
    showDialog () {
      this.dialog = true
    }
  }
}
</script>