<template>
  <admin-template
    pageName="Meu Perfil"
    alias="profile"
    cssClass="pa-0"
  >
    <div class="profile">
      <div class="profile-content px-4 px-sm-8 pb-4">
        <card 
          class="profile-card"
          title="Informaes pessoais"
          icon="account_box"
        >
          <v-row>
            <v-col cols="12" sm="5" md="4" class="text-center">
              <div class="profile-image-content d-inline-block text-center">
                <img v-if="imageUrl" :src="imageUrl">
              </div>

              <div class="btn-upload-box">
                <btn-confirm
                  text="Alterar Imagem"
                  class="mt-6"
                  icon="cloud_upload"
                  @clicked="toggleShow"
                />
              </div>

              <upload-image
                field="img"
                @crop-success="cropSuccess"
                @crop-upload-success="cropUploadSuccess"
                @crop-upload-fail="cropUploadFail"
                v-model="show"
                :width="300"
                :height="300"
                lang-type="pt-br"
                :url="uploadUrl"
                :params="params"
                :headers="headers"
                img-format="png"
              ></upload-image>
            </v-col>
            <v-col cols="12" sm="7" md="8" lg="6" class="pl-lg-8">
              <v-form>
                <v-row>
                  <v-col cols="12" class="py-0">
                    <input-field
                      label="Nome"
                      :rules="nameRules"
                      :value="barber.name"
                      v-model="name"
                      @onKeyUp="setName"
                      counter="50"
                    />
                  </v-col>

                  <v-col cols="12" sm="12" md="7" class="py-0">
                    <input-field
                      label="E-mail"
                      :disabled="true"
                      :value="barber.email"
                    />
                  </v-col>

                  <v-col cols="12" sm="12" md="5" class="py-0">
                    <phone-field
                      label="Telefone"
                      :rules="phoneRules" 
                      :value="barber.phone"
                       @onKeyUp="setPhone"
                    />
                  </v-col>

                  <v-col cols="12" class="py-0">
                    <textarea-field
                      label="Perfil"
                      :rules="profileRules"
                      :value="barber.profile"  
                    />
                  </v-col>
                  <v-row class="ma-0">
                    <v-spacer></v-spacer>
                    <v-col cols="12" sm="auto">
                      <btn-secondary
                        text="Salvar" 
                        class="mt-4"
                        :disabled="btnDisabled"
                        @clicked="updateBarber"
                      />
                    </v-col>
                    <v-col cols="12" sm="auto">
                      <btn-text
                        text="Cancelar" 
                        class="mt-0 mt-sm-4"
                        :disabled="btnDisabled"
                        @clicked="updateBarber"
                      />  
                    </v-col>  
                  </v-row>
                </v-row>
              </v-form>
            </v-col>
          </v-row>
        </card>
      </div>
    </div>
    <overlay ref="overlay" />
  </admin-template>
</template>

<script>
import Axios from '@/plugins/axios'

// Components
import AdminTemplate from '@/components/admin/template/Template'
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import BtnText from '@/components/globais/buttons/text/BtnText'
import Card from '@/components/globais/card/Card'
import InputField from '@/components/globais/form/input-field/InputField'
import Overlay from '@/components/globais/overlay/Overlay'
import PhoneField from '@/components/globais/form/phone-field/PhoneField'
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'
import UploadImage from 'vue-image-crop-upload'

// Mixins
import BarberMixin from '@/mixins/BarberMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import OverlayMixin from '@/mixins/OverlayMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TextMixin from '@/mixins/TextMixin'

export default {
  name: 'Profile',
  components: { AdminTemplate, BtnConfirm, BtnSecondary, BtnText, Card, InputField, UploadImage, Overlay, PhoneField, TextareaField },
  mixins: [ BarberMixin, NavigationMixin, OverlayMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    name: '',
    email: '',
    profile: '',
    phone: '',
    barber: {
      id: '',  
      email: '',
      name: '',
      profile: '',
      phone: '',
      image_url: ''
    },
    nameRules: [
      v => !!v || 'Informe seu nome',
      v => (v && v.length <= 50) || 'O campo nome deve ter no mximo 50 caracteres'
    ],
    phoneRules: [
      v => !!v || 'Informe seu telefone'
    ],
    profileRules: [
      v => (v && v.length <= 200) || 'O seu perfil deve ter no mximo 50 caracteres'
    ],
    show: false,
    params: {
      image: ''
    },
    headers: {
      token: '',
      smail: '*_~'
    },
    uploadUrl: '',
    imgDataUrl: '',
    imageKey: 0
  }),
  computed: {
    btnDisabled () {
      if (!this.name || this.name.length > 50) return true
      
      let phone = this.removeSimbols(this.phone)
      if (!phone || (phone.length != 10 && phone.length != 11)) return true
      
      return false
    },
    imageUrl () {
      if (!this.barber.image_url) return null
      return `${this.$url_backend}${this.barber.image_url}`
    }
  },
  mounted () {
    this.headers.token = localStorage.token
    this.uploadUrl = this.$url_api + '/barber/image'
    setTimeout(() => {
      this.barber = this.getBarber()
      this.name = this.barber.name
      this.email = this.barber.email
      this.phone = this.barber.phone
      this.profile = this.barber.profile
    }, 300)
  },
  methods: {
    onOverlaySuccess (text) {
      this.showSnackbar(text)
    },
    setError (text) {
      this.showSnackbarErro(text)
      this.hideOverlayError(text)
    },
    setPhone (val) {
      this.phone = val
    },
    setName (val) {
      this.name = val
    },
    updateBarber () {
      this.showOverlay('Salvando dados')
      Axios.put(`/barber/${this.barber.id}`, {
        name: this.name,
        phone: this.removePhoneMask(this.phone),
        profile: this.profile
      })
      .then(response => {
        if (response.data.codigo === 200) {
          let token = response.data.corpo.token
          this.storeBarber(token)
          this.getBarber()
          let text = 'Perfil alterado com sucesso!'
          this.hideOverlaySuccess(text)
        } else this.setError(response.data.corpo)
      })
      .catch ((err) => {
        this.setError('No foi possvel alterar seus dados!')
        window.console.log(err)
      })
    },
    toggleShow() {
			this.show = !this.show
		},
    cropSuccess(imgDataUrl){
      this.imgDataUrl = imgDataUrl
      this.params.image = imgDataUrl
    },
    cropUploadSuccess (response) {
      let token = response.corpo
      this.storeBarber(token)
      this.barber = this.getBarber()
      this.hideOverlaySuccess('Imagem alterado com sucesso!')
    },
    cropUploadFail(status, field) {
      window.console.log('-------- upload fail --------')
      window.console.log(status)
      window.console.log('field: ' + field)
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

.profile { position: relative; display: block;
  &:before {
    content: '';
    background: $primary;
    height: calc(100vh / 100 * 35);
    position: fixed;
    display: block;
    top: 64px;
    width: 100%;
  }

  .profile-card { margin-top: calc(100vh / 100 * 10);
    .profile-image-content {
      width: 150px;
      height: 150px;
      @include box-shadown-mixin(.8);
      @include border-radius-mixin(50%);
      border-radius: 50%;
      border: 1px solid lighten($color: $primaryLight, $amount: 30);
      overflow: hidden;
    }
    .btn-upload-box { width: fit-content; margin: auto; }
  }
}

@media all and (max-width: 959px) {
  .profile {
    &:before { top: 56px; }
    .profile-card { margin-top: 60px; }
  }
}

@media all and (max-width: 599px) {
  .profile .profile-card { margin-top: 15px; }
}
</style>