<style scoped>
#menu-lateral {
  padding: 0 20px;
}

#menu-lateral .app-logo {
  margin: 30px auto;
  max-width: 100%;
  display: block;
}
</style>
<template>
  <v-navigation-drawer
    id="menu-lateral"
    color="primaryDark"
    :class="fullDrawer ? 'open' : ''"
    v-model="drawer"
    clipped
    :permanent="isPermanent"
    fixed
    app
  >
    <v-list dense>
      <template v-for="item in items">
        <v-list-item
          class="btn-lateral my-1"
          :class="isActive(item.alias)"
          :key="item.text"
          @click="goTo(item.routeName)"
          rounded
          dark
        >
          <v-list-item-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              {{ item.text }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
// Modules
import './NavigationDrawer.scss'
// Mixins
import MenuMixin from '@/mixins/MenuMixin'
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'NavigationDrawer',
  mixins: [ MenuMixin, NavigationMixin ],
  props: {
    alias: {
      default: ''
    }
  },
  data: () => ({
    isPermanent: true,
    drawer: false,
    fullDrawer: false,
    items: [
      { icon: 'today', text: 'Agenda', routeName: 'admin-schedule', alias: 'schedule' },
      { icon: 'pending_actions', text: 'Aguardando', routeName: 'admin-approval', alias: 'approval' },
      { icon: 'build', text: 'Preos e Servios', routeName: 'admin-service', alias: 'service' },
      { icon: 'miscellaneous_services', text: 'Configuraes', routeName: 'admin-barbershop', alias: 'dashboard' },
      { icon: 'attach_money', text: 'Planos', routeName: 'admin-plans', alias: 'plans' },
      { icon: 'help', text: 'Ajuda', routeName: 'admin-help', alias: 'help' }
    ]
  }),
  mounted () {
    this.fullDrawer = this.getDrawer()
    this.isPermanent = window.innerWidth > 959 ? true : false
  },
  methods: {
    isActive (itemAlias) {
      return this.alias === itemAlias ? 'active' : ''
    },
    toggleNavigationDrawer () {
      this.drawer = !this.drawer
      this.fullDrawer = !this.fullDrawer
      this.setDrawer(this.fullDrawer)
    }
  }
}
</script>